.svg-search_btn {
  height: 18px;
  width: 18px;
  stroke: #5A5858;
  fill: transparent;
}

.ag-header-search_btn:hover .svg-search_btn {
  stroke: #FFF;
}

.svg-login {
  height: 16px;
  width: 14px;
  margin: 0 5px -2px 0;
  stroke: #FFF;
}

.ag-login_link:hover .svg-login {
  stroke: #5A5858;
}

.svg-star {
  height: 16px;
  width: 16px;
  margin: 0 auto;
  stroke: #FFF;
}

.svg-clock {
  height: 16px;
  width: 16px;
  margin: 0 auto;
  stroke: #FFF;
}

.svg-message {
  height: 16px;
  width: 16px;
  margin: 0 auto;
  stroke: #FFF;
}

.svg-arrow {
  height: 4px;
  width: 8px;
  margin: -2px 0 0;
  stroke: #FFF;
  position: absolute;
  top: 50%;
  right: 0;
}

.js-ag-footer-middle_title__active .svg-arrow {
  transform: scaleY(-1);
}

.svg-filter {
  display: inline-block;
  height: 14px;
  width: 14px;
  margin: 0 7px 0 0;
  vertical-align: text-top;
  stroke: #FFF;
  fill: transparent;
}

.svg-more {
  height: 4px;
  width: 16px;
  margin: 0 0 3px;
  fill: #A19FA0;
}

.js-ag-tag-search_item__more:hover .svg-more {
  fill: #FFF;
}

.svg-grid-4,
.svg-grid-9,
.svg-grid-16 {
  height: 16px;
  width: 16px;
  fill: #5A5858;
}

.js-ag-grid-btn_item__active .svg-grid,
.ag-grid-btn_item:hover .svg-grid-4,
.ag-grid-btn_item:hover .svg-grid-9,
.ag-grid-btn_item:hover .svg-grid-16 {
  fill: #FFF;
}

.svg-gl {
  height: 18px;
  width: 18px;
  margin: 0 10px -3px 0;
}